<!--
    This component serves to list all transaction of contacts.
    Depending on the screen size, transaction will be displayed as a virtual scroll or as a drop down list.

    props:
        abi: Array

-->

<template>

    <div>
        <v-subheader>Transactions</v-subheader>

        <v-virtual-scroll :items="filteredAbi" :bench="5" :item-height="50" height="inherit" min-height="300" :max-height="600" class="full-list hidden-xs-only">
            <template v-slot:default="{ item }">
                <v-list-item :key="item.name" @click="selected(item)">
                    <v-list-item-content :class="item.name == transaction.name ? 'active' : ''">
                        <span :style="colorItem(item.stateMutability)">{{ item.name }}</span>
                    </v-list-item-content>
                </v-list-item>
            </template>
        </v-virtual-scroll>

        <div class="compact-list hidden-sm-and-up">
            <v-tooltip color="info" bottom>
                <template v-slot:activator="{ on, attrs }">
                    <div v-bind="attrs" v-on="on">
                        <v-select
                            v-model="transaction"
                            :items="filteredAbi"
                            label="Transaction"
                            item-text="name"
                            return-object
                            single-line
                            dense
                        >
                            <template v-slot:item="{ item }">
                                <span :style="colorItem(item.stateMutability)">{{ item.name }}</span>
                            </template>
                            <template v-slot:selection="{ item }">
                                <span :style="colorItem(item.stateMutability)">{{ item.name }}</span>
                            </template>
                        </v-select>
                    </div>
                </template>
                <span>Select transaction</span>
            </v-tooltip>
        </div>
    </div>

</template>

<script>

    export default {
        name: 'TransactionList',

        props: {
            abi: Array
        },

        data() {
            return {
                transaction: '',
                name: ''
            }
        },

        computed: {
            filteredAbi() {
                const tmpAbi = [];

                if (this.abi) {
                    this.abi.map( a => {
                        if (a.type !== 'constructor' && a.type !== 'event') {
                            tmpAbi.push(a);
                        }
                    });
                }

                return tmpAbi;
            }
        },

        watch: {
            transaction(val) {
                this.name = val.name;
                this.$emit('transactionSelected', val);
            }
        },

        methods: {
            selected(t) {
                this.transaction = t;
            },

            colorItem(state) {
                let clr = '';

                switch (state) {
                    case 'view':
                        clr = 'color: var(--v-primary-lighten1)';
                        break;
                    case 'pure':
                        clr = 'color: var(--v-info-lighten1)';
                        break;
                    case 'nonpayable':
                        clr = 'color: var(--v-accent-lighten1)';
                        break;
                    case 'payable':
                        clr = 'color: var(--v-error-lighten1)';
                }

                return clr;
            }
        }

    }

</script>

<style scoped>

    .full-list {
        min-width: fit-content;
    }

    .compact-list {
        padding-left: 25px;
        padding-right: 25px;
    }

    .v-list-item__content {
        text-overflow: ellipsis;
        white-space: nowrap;
        overflow: hidden;
        max-width: 155px;
        display: block;
    }

    .active {
        box-shadow: var(--v-accent-base) 5px 0px 0px inset !important;
        padding-left: 10px;
        color: var(--v-primary-base) !important;
    }

</style>
